import { NextRequest, NextResponse } from 'next/server';
import { Pool } from 'pg';

// Initialize database connection
const pool = new Pool({
  user: process.env.DB_USER,
  host: process.env.DB_HOST,
  database: process.env.DB_NAME,
  password: process.env.DB_PASSWORD,
  port: parseInt(process.env.DB_PORT || '5432'),
});

export async function POST(request: NextRequest) {
  try {
    const { email, password } = await request.json();

    if (!email || !password) {
      return NextResponse.json(
        {
          success: false,
          error: 'Email and password are required'
        },
        { status: 400 }
      );
    }

    // Check if user exists in database and has moderator role
    const client = await pool.connect();
    try {
      const query = `
        SELECT id, name, email, role, created_at, updated_at, avatar, google_id, 
               permissions FROM users 
        WHERE email = $1 AND role IN ('moderator', 'admin')
      `;
      const result = await client.query(query, [email]);

      if (result.rows.length === 0) {
        return NextResponse.json(
          {
            success: false,
            error: 'Invalid credentials or insufficient privileges'
          },
          { status: 401 }
        );
      }

      const user = result.rows[0];
      
      // In a real app, you would compare hashed passwords here
      // For this mock implementation, we'll assume password is valid
      // if the user exists with moderator/admin role
      if (password !== process.env.MODERATOR_DEFAULT_PASSWORD && 
          email !== process.env.ADMIN_EMAIL) {
        return NextResponse.json(
          {
            success: false,
            error: 'Invalid credentials'
          },
          { status: 401 }
        );
      }

      // Generate a token for the authenticated moderator
      const token = 'mock-jwt-token-moderator';

      return NextResponse.json(
        {
          success: true,
          data: { 
            user: {
              ...user,
              permissions: user.permissions || {
                can_edit_products: user.role === 'admin',
                can_edit_reviews: true,
                can_edit_orders: true,
                can_edit_payments: user.role === 'admin',
              }
            }, 
            token 
          }
        },
        { status: 200 }
      );
    } finally {
      client.release();
    }
  } catch (error) {
    console.error('Moderator login error:', error);
    return NextResponse.json(
      {
        success: false,
        error: 'Internal server error'
      },
      { status: 500 }
    );
  }
}